import React from 'react'
import { useParams } from 'react-router-dom'
import {CardElement, Elements, ElementsConsumer, PaymentElement, PaymentRequestButtonElement, useElements, useStripe} from '@stripe/react-stripe-js';
import {loadStripe} from '@stripe/stripe-js';
import axios from 'axios';
import './strapi.css'

export const Strapi = () =>{
    const monto = useParams()

    const mont = Number(Object.values(monto))

    const stripePromise = loadStripe('pk_test_51KaLSeBDTCuuGArIhkskcqG9J0VNElLpQh5qkqCR3UnQak6T8u6qgGGxlpXXYpp91phf1TfV5xSocebr7fGxGFhF00vYyWA20a');
    //////////
    const CheckoutForm = () =>{

        const stripe = useStripe();
        const elements = useElements();

        const handleSubmit = async (e) =>{
            e.preventDefault()

            const {error,paymentMethod} = await stripe.createPaymentMethod({
                type:'card',
                card:elements.getElement(CardElement)
            })

            if(!error){
                const {id} = paymentMethod

                const {data} = await axios.post(`http://localhost:27017/api/pago/`,{
                    id,
                    amount:mont
                })
                console.log(data)
            }
        }

    return (
        <form onSubmit={handleSubmit} className='col-sm-12 container'>
            <div className='card-desktop col-sm-6 m-auto'>
            <div className='col-sm-10 m-auto'>
                <CardElement id='tarjeta-estilo' className=''/>
                <p className='post-code mt-2'>si tu C.P tiene 4 digitos poner un 0 adelante Ejemplo: 01882</p>
            </div>
            <div className='text-center'>
            <div>
                <p className='precio-checkout'>Precio: 3 USD</p>
            </div>
            <div>
                <button id='btn-buy'>Comprar</button>
            </div>
            </div>
            </div>
        </form>           
    );
    }

    //////
    return(
        <div className='background-card'>
        <Elements stripe={stripePromise}>
            <CheckoutForm />
        </Elements>
        </div>
    )
}